import {
  Avatar,
  Box,
  Button,
  Checkbox,
  Divider,
  FormControlLabel,
  Grid,
  ListItem,
  ListItemAvatar,
  ListItemText,
  makeStyles,
  TextareaAutosize,
  TextField,
  Typography,
} from "@material-ui/core";
import React, { useContext, useEffect, useState } from "react";
import { useMutation, useQuery } from "react-query";
import { Link, useHistory } from "react-router-dom";
import AddNewAddress from "../components/addNewAddress";
import { CartContext } from "../contexts/CartContext";
import getUserAddresses from "../services/user/getUserAddresses";
import Loading from "../components/loading";
import createOrder from "../services/user/createOrder";
import { toast } from "react-toastify";
import { UserContext } from "../contexts/UserContext";
import api from "../services/api";
import getCustomerPrivilege from "../services/user/getCustomerPrivilage";
import axios from "axios";

const useStyles = makeStyles((theme) => ({
  container: {
    margin: "2rem 5rem",
    [theme.breakpoints.down("xs")]: {
      margin: "3rem 1rem",
    },
  },
  userSelections: {
    width: "40%",
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
  cartItems: {
    marginTop: "2rem",
    padding: "10px",
    border: "2px solid grey",
    width: "100%",
  },
}));

const Checkout = () => {
  const classes = useStyles();
  const [showAddAddressBox, setShowAddAddressBox] = useState(false);
  const [deliveryMethod, setDeliveryMethod] = useState({
    home: true,
    pickup: false,
  });
  const [paymentMethod, setPaymentMethod] = useState({
    COD: false,
    Bank: false,
    ESewa: true,
  });
  const [shipping_id, setShippingId] = useState(null);
  const [billing_id, setBillingId] = useState(null);
  const [deliveryCharge, setDeliveryCharge] = useState(null);
  const [emergency_delivery, setEmergencyDelivery] = useState(false);
  const [insideValley, setInsideValley] = useState(true);
  const [note, setNote] = useState("");
  const [codDisabled, setIsCodDisabled] = useState(false);

  const [showDiscountBox, setShowDiscountBox] = useState(false);
  const [promocode, setPromocode] = useState("");
  const [promocodeDetails, setPromocodeDetails] = useState()

  const [UserState] = useContext(UserContext);
  const history = useHistory();

  const { total, cartItems, handleCheckout } = useContext(CartContext);

  useEffect(() => {
    if (cartItems && cartItems.length > 0) {
      let findIfAnyCartItemHasDiscount = cartItems.find(
        (item) => item.discount_amount > 0
      );

      if (findIfAnyCartItemHasDiscount) {
        setShowDiscountBox(false);
      } else {
        setShowDiscountBox(true);
      }
    }
  }, [cartItems]);

  const { isLoading, data } = useQuery("userAddresses", () =>
    getUserAddresses()
  );

  const { isLoading: isCustomerPrivilegeLoading, data: customerPrivilege } =
    useQuery("cusomterPrivilege", () => getCustomerPrivilege());

  const getSpecialDiscount = (customerPrivilege) => { };

  const mutation = useMutation((order) => createOrder(order));

  const calculateDeliveryCharge = () => {
    if (
      customerPrivilege &&
      customerPrivilege.length > 0 &&
      customerPrivilege[0].free_delivery === true
    ) {
      return 0;
    }
    if (total >= 10000) {
      return 0;
    }
    if (emergency_delivery) {
      return Number(deliveryCharge.emergency_delivery);
    } else {
      if (insideValley) {
        return Number(deliveryCharge.inside_valley);
      } else {
        return Number(deliveryCharge.outside_valley);
      }
    }
  };

  useEffect(() => {
    if (UserState.user == null) {
      history.push("/signin");
    }
    async function getDeliveryCharge() {
      try {
        const response = await api.get(`/orders/delivery-cost/`);
        setDeliveryCharge(response.data[0]);
      } catch (err) {
        console.log("error getting delivery charge", err);
      }
    }
    getDeliveryCharge();
  }, [UserState.user, history]);

  const handleShippingMethodChange = (e) => {
    if (e.target.name === "homeDelivery") {
      setDeliveryMethod({ home: true, pickup: false });
    }
    if (e.target.name === "pickup") {
      setDeliveryMethod({ home: false, pickup: true });
    }
  };

  const handlePaymentMethodChange = (e) => {
    if (e.target.name === "COD") {
      setPaymentMethod({ COD: true, ESewa: false, Bank: false });
    }
    if (e.target.name === "Bank") {
      setPaymentMethod({ COD: false, ESewa: false, Bank: true });
    }
    if (e.target.name === "ESewa") {
      setPaymentMethod({ COD: false, Bank: false, ESewa: true });
    }
  };

  const handleAddAddressClose = () => {
    setShowAddAddressBox(false);
  };

  const setAddressInfo = (addressType, id) => {
    if (addressType === "shipping") {
      setShippingId(id);
    }

    if (addressType === "billing") {
      setBillingId(id);
    }
  };

  const handlePromocodeChange = (e) => {
    setPromocode(e.target.value);
  };

  async function handlePromocodeApply() {
    if (!promocode) {
      toast.error("Promo code cannot be empty!");
    } else if (promocodeDetails) {
      toast.error("A promo code is already applied!");
    } else {
      await api
        .get(
          `${process.env.REACT_APP_API_URL}/orders/promo-code-details/?promo_code=${promocode}`
        )
        .then((res) => {
          if (res.data && res.data.length > 0) {
            setPromocodeDetails(res.data[0]);
          }
        })
        .catch((err) => {
          toast.error("Invalid promo code");
        });
    }
  };

  const handlePromocodeRemove = () => {
    setPromocode("");
    setPromocodeDetails(null)
  }

  const handleOrder = () => {
    let data = [];
    let delivery_method = "";
    let payment_method = "";

    cartItems.forEach((product) => {
      data.push({
        product_id: product.id,
        qty: `${product.quantity}`,
      });
    });

    if (deliveryMethod.home === true) {
      delivery_method = "Ship";
    } else {
      delivery_method = "Pickup";
    }

    if (paymentMethod.COD === true) {
      payment_method = "COD";
    }
    if (paymentMethod.ESewa === true) {
      payment_method = "Esewa";
    }
    if (paymentMethod.Bank === true) {
      payment_method = "BankPayment";
    }

    const payload = {
      data,
      shipping_id,
      billing_id,
      delivery_method,
      payment_method,
      emergency_delivery,
      note,
    };

    let payloadWithPromocodeDiscount;

    if (promocodeDetails) {
      let { promo_code } = promocodeDetails;

      payloadWithPromocodeDiscount = {
        data,
        shipping_id,
        billing_id,
        delivery_method,
        payment_method,
        emergency_delivery,
        note,
        promo_code,
      }
    }
    if (!shipping_id && !billing_id) {
      toast.error("Please provide billing and shipping addresses");
      return;
    }

    mutation.mutate(promocodeDetails ? payloadWithPromocodeDiscount : payload, {
      onSuccess: (res) => {
        history.push({
          pathname: "/profile",
          state: { personalInfo: false, addressInfo: false, myOrders: true },
        });
        handleCheckout();
        toast.info("Order created successfully");
      },
      onError: (error) => {
        toast.error("Couldnt create your order");
        console.log(error);
      },
    });
  };

  const showAddresses = (addressType) => {
    if (isLoading) {
      return null;
    } else {
      return (
        data &&
        data.map((item, i) => (
          <Box
            display="flex"
            alignItems="flex-start"
            flexWrap="wrap"
            key={`${i}`}
            style={{ marginRight: "2rem" }}
          >
            <Checkbox onClick={() => setAddressInfo(addressType, item.id)} />
            <Box>
              <Typography color="textSecondary" variant="subtitle1">
                {item.fullname}
              </Typography>
              <Typography color="textSecondary" variant="subtitle1">
                {item.address}
              </Typography>
              <Typography color="textSecondary" variant="subtitle1">
                {item.nearest_landmark}
              </Typography>
              <Typography color="textSecondary" variant="subtitle1">
                {item.country}
              </Typography>
              <Typography color="textSecondary" variant="subtitle1">
                {item.phone}
              </Typography>
            </Box>
          </Box>
        ))
      );
    }
  };

  const showCartItems = () =>
    cartItems.map((item) => (
      <ListItem key={item.id}>
        <ListItemAvatar>
          <Avatar
            src={`${process.env.REACT_APP_UPLOAD_URL}${item.images[0].image}`}
            imgProps={{
              alt: item.brand.brand_name,
            }}
          />
        </ListItemAvatar>
        <ListItemText
          primary={item.product_name}
          secondary={`NPR. ${item.price - item.discount_amount}`}
          secondaryTypographyProps={{ color: 'primary' }}
        />

        <Typography variant="h6" color="textSecondary">
          Qty: {item.quantity}
        </Typography>
      </ListItem>
    ));

  const getCustomerPrivilage = () => {
    const discountedItems = cartItems.filter(
      (item) => item.discount_amount > 0
    );

    if (discountedItems.length > 0) {
      return 0;
    }
    if (customerPrivilege && customerPrivilege.length > 0) {
      return customerPrivilege[0].special_discount;
    }
  };

  if (isCustomerPrivilegeLoading) {
    return <Loading />;
  }

  const getSubTotalWithPromocodeDiscount = () => {
    let subTotal = 0;

    if (promocodeDetails) {
      subTotal += parseInt(total) - ((promocodeDetails.discount_percentage / 100) * parseInt(total)) + calculateDeliveryCharge()
    }

    return subTotal;
  }

  return (
    <div className={classes.container}>
      <Typography variant="h6" style={{ fontSize: "1.8rem" }} gutterBottom>
        Checkout Details
      </Typography>
      <Divider />
      <Typography
        variant="h6"
        color="textSecondary"
        style={{ margin: "1rem 0" }}
      >
        Shipping Method
      </Typography>
      <Box display="flex" className={classes.userSelections}>
        <FormControlLabel
          control={
            <Checkbox
              checked={deliveryMethod.home}
              onChange={handleShippingMethodChange}
              name="homeDelivery"
            />
          }
          label="Home Delivery / Courier"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={deliveryMethod.pickup}
              onChange={handleShippingMethodChange}
              name="pickup"
            />
          }
          label="Pickup Yourself"
        />
      </Box>
      <Divider style={{ marginTop: "1rem" }} />
      <Typography
        variant="h6"
        color="textSecondary"
        style={{ margin: "1rem 0" }}
      >
        Shipping Address
      </Typography>
      <Box display="flex" className={classes.userSelections}>
        {isLoading ? <Loading /> : showAddresses("shipping")}
      </Box>
      <Box
        display="flex"
        justifyContent="flex-end"
        style={{ marginTop: "1rem" }}
      >
        <Button variant="outlined" onClick={() => setShowAddAddressBox(true)}>
          + Add New Address
        </Button>
      </Box>
      <Divider style={{ marginTop: '1rem' }} />
      <Typography
        variant="h6"
        color="textSecondary"
        style={{ margin: "1rem 0" }}
      >
        Are you inside valley?
      </Typography>
      <Box display="flex" className={classes.userSelections}>
        <FormControlLabel
          control={
            <Checkbox
              checked={!insideValley}
              onChange={() => {
                setInsideValley(false);
                setIsCodDisabled(true);
              }}
            />
          }
          label="No"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={insideValley}
              onChange={() => {
                setInsideValley(true);
                setIsCodDisabled(false);
              }}
            />
          }
          label="Yes (Kathmandu, Bhaktapur, Lalitpur)"
        />
      </Box>
      <Divider style={{ marginTop: "1rem" }} />
      <Typography
        variant="h6"
        color="textSecondary"
        style={{ margin: "1rem 0" }}
      >
        Payment Method
      </Typography>
      <Box display="flex" className={classes.userSelections}>
        <FormControlLabel
          control={
            <Checkbox
              checked={paymentMethod.COD}
              onChange={handlePaymentMethodChange}
              name="COD"
              disabled={codDisabled}
            />
          }
          label="Cash on Delivery"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={paymentMethod.Bank}
              onChange={handlePaymentMethodChange}
              name="Bank"
            />
          }
          label="Online (Bank/QR) "
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={paymentMethod.ESewa}
              onChange={handlePaymentMethodChange}
              name="ESewa"
            />
          }
          label="ESewa"
        />
      </Box>
      <Divider style={{ marginTop: '1rem' }} />

      <Typography
        variant="h6"
        color="textSecondary"
        style={{ margin: "1rem 0" }}
      >
        Note
      </Typography>
      <Box display="flex" className={classes.userSelections}>
        <TextareaAutosize
          rows={4}
          style={{ width: "95%", padding: "10px" }}
          aria-label="maximum height"
          value={note}
          onChange={(e) => setNote(e.target.value)}
        />
      </Box>
      <AddNewAddress
        open={showAddAddressBox}
        handleClose={handleAddAddressClose}
      />
      <Grid container justify="space-between">
        <Grid
          item
          sm={12}
          md={showDiscountBox ? 4 : 6}
          className={classes.cartItems}
        >
          <Typography variant="h5" gutterBottom>
            Cart Items
          </Typography>
          {showCartItems()}
        </Grid>
        {showDiscountBox ? (
          <Grid item sm={12} md={3} className={classes.cartItems}>
            <Typography variant="h5" gutterBottom>
              Promo code discount
            </Typography>
            <Grid container justify="space-between">
              <TextField
                id="outlined-basic"
                variant="outlined"
                name="promocode"
                value={promocode}
                onChange={handlePromocodeChange}
                placeholder="Enter promo code"
                size="small"
                style={{ width: "75%" }}
              />
              <Button
                variant="contained"
                color="primary"
                onClick={() => handlePromocodeApply()}
              >
                Apply
              </Button>
            </Grid>
            <Typography variant="subtitle1" style={{ paddingTop: "30px" }}>
              Conditions appled:
            </Typography>
            <Typography variant="subtitle1" style={{ color: "#FF0000" }}>
              1. When promo code is used, special user discount does not apply
              either.
            </Typography>
          </Grid>
        ) : null}

        <Grid
          item
          sm={12}
          md={showDiscountBox ? 3 : 5}
          className={classes.cartItems}
        >
          <Typography variant="h6">
            <Typography
              variant="subtitle1"
              color="textSecondary"
              component="span"
              style={{ marginRight: "1rem" }}
            >
              Subtotal:
            </Typography>
            NPR. {total}
          </Typography>
          <Typography variant="h6">
            <Typography
              variant="subtitle1"
              color="textSecondary"
              component="span"
              style={{ marginRight: "1rem" }}
            >
              Shipping:
            </Typography>
            {deliveryCharge &&
              (customerPrivilege &&
                customerPrivilege.length > 0 &&
                customerPrivilege[0].free_delivery === true ? (
                <Typography variant="h6" color="primary">
                  Congratulations, you're a special customer! Free delivery for
                  you
                </Typography>
              ) : (
                <Typography> NRP. {calculateDeliveryCharge()}</Typography>
              ))}
          </Typography>
          <Divider style={{ margin: "1rem 0" }} />
          <Typography variant="h6">
            <Typography
              variant="h6"
              color="textSecondary"
              component="span"
              style={{ marginRight: "1rem" }}
            >
              Total:
            </Typography>

            {
              promocodeDetails ? (
                <>
                  <Typography variant="h6" color="primary">
                    Promo code discount:{" "}
                    {promocodeDetails.discount_percentage}% <span style={{ color: "#FF0000", cursor: "pointer" }} onClick={() => handlePromocodeRemove()}>(Remove)</span>
                  </Typography>
                  NPR. {getSubTotalWithPromocodeDiscount()}
                </>
              ) : (
                <>
                  {getCustomerPrivilage() > 0 &&
                    customerPrivilege &&
                    customerPrivilege.length > 0 &&
                    customerPrivilege[0].special_discount > 0 && (
                      <Typography variant='h6' color='primary'>
                        Special discount:{' '}
                        {customerPrivilege &&
                          customerPrivilege.length > 0 &&
                          customerPrivilege[0].special_discount}
                        % (For you)
                      </Typography>
                    )}
                  {deliveryCharge && (
                    <Typography>
                      NPR.{' '}
                      {parseInt(total) +
                        calculateDeliveryCharge() -
                        (getCustomerPrivilage() / 100) * parseInt(total)}
                    </Typography>
                  )}
                </>
              )
            }



          </Typography >
          <Button
            variant="contained"
            color="primary"
            style={{ margin: "1rem 0" }}
            onClick={handleOrder}
          >
            Complete Checkout
          </Button>
          <Divider style={{ margin: "1rem 0" }} />
          <Typography variant="subtitle1">
            By completing checkout, you state that you agree to our{" "}
            <Link to="/" style={{ color: "red" }}>
              Terms and Conditions.
            </Link>
          </Typography>
        </Grid >
      </Grid >
    </div >
  );
};

export default Checkout;
